<template>
    <div id="listUser" class="row justify-content-md-center my-4">
        <li v-for="user in dataJson" :key="user.index">
            <User :user="user"></User>
        </li>
    </div>
</template>

<script>
import {fakeJsonOb} from '@/fakeData/fakeJson'
import User from '@/components/User'

export default {
  name: 'ListUser',
  components: {
    User
  },
  // we will use a fake json file for example user data
  data () {
    return {
      dataJson: fakeJsonOb
    }
  }
}
</script>

<style scoped>
#listUser {
    display: flex;
    flex-wrap: wrap-reverse;
    margin: auto;
}

#listUser li {
  list-style-type: none;
  display: flex;
}

</style>



