<template>
  <div class="user-card">
    <div class="card" style="width: 18rem;">
        <div class="card-body">
          <h5 class="card-title">{{user.name}}</h5>
          <p class="card-text">{{user.address}}</p>
        </div>
        <ul class="list-group list-group-flush">
          <li class="list-group-item">Phone: {{user.phone}}</li>
          <li class="list-group-item">Gender: {{user.gender}}</li>
          <li class="list-group-item">Email: {{user.email}}</li>
          <li class="list-group-item">Favorite: {{user.favoriteFruit}}</li>
        </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'User',
  props: {
    user: Object
  },
  data () {
    return {
      mess: 'user comp'
    }
  },
  methods: {
    deleteUser () {
      var dataEvent = {
        id: this.user.id,
        name: 'Each user'
      }
      console.log('even delete emit from User.vue')
      this.$emit('delete-user', dataEvent)
    }
  }
}
</script>

<style scoped>
.user-card {
  display: inline-block;
  border: solid 1px #ddd;
  position: relative;
}

button {
  position: absolute;
  top: 0px;
  right: 0px;
}
</style>



